body {
  justify-content: center;
  align-items: top;
  background-color: #2e3440;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
  overflow: hidden;
}

.knobs {
  grid-template-rows: repeat(16, 46px);
  grid-auto-flow: column;
  justify-items: center;
  gap: 0 4px;
  display: grid;
}

.menu {
  flex-flow: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
}

#hostaddr {
  text-align: right;
  color: #2e3440;
  background-color: #81a1c1;
  border: none;
  width: 20vw;
  transition: all .2s ease-out;
}

#hostaddr:focus {
  color: #81a1c1;
  background-color: #4c566a;
  outline: none;
  width: 65vw;
  font-size: 2.5rem;
}

#reloadbutton {
  color: #2e3440;
  background-color: #81a1c1;
  border: none;
  width: 20vw;
}

.faders {
  grid-template-columns: repeat(8, 44px);
  justify-items: center;
  display: grid;
}
